import { Component, h, Element } from '@stencil/core/internal';
import tailwind from '../../output.css';
@Component({
  tag: 'rf-display-hotel',
  shadow: true,
})
export class DisplayHotel {
  @Element() element: HTMLElement;

  componentDidLoad() {
    const shadowRoot = this.element.shadowRoot;
    if (shadowRoot) {
      const style = document.createElement('style');
      style.textContent = tailwind;
      shadowRoot.appendChild(style);
    }
  }
  render() {
    return (
      <div class='flex flex-col mx-0 lg:mx-20 xl:mx-60'>
        <rf-navbar></rf-navbar>
        <rf-photo></rf-photo>
        <rf-rooms></rf-rooms>
        <rf-facilities></rf-facilities>
      </div>
    );
  }
}
